import React, { Component } from 'react'
import Server from '../server'

export default class Logout extends Component {
  constructor(props){
    super(props)


    this.server = new Server()
  }


  logout = ()=>{
    this.server.logout()
    .then(data=>{
      console.log("Logout del componente")
      console.log(data)
    })
    .catch((err)=>{console.log(err)})
  }





  render() {

   this.logout()



    return (
      <div>
        
      </div>
    )
  }
}
